<!-- 我的页面 -->
<template>
	<view class="my">
		<!-- banner图 -->
		<view class="banner">
			<!-- 头像用户名 -->
			<view class="headBox" @click="toLogin">
				<image :src="avatarUrl" />
				<view class="userName">{{nickName}}</view>
			</view>
		</view>
		<!-- 地址簿，实名认证，专属快递员 -->
		<view class="list-top">
			<view class="list-item" @click="handleToAddress">
				<view class="left">
					<image class="icon" src='../../static/my-address.png' />
					<view class="label">地址簿</view>
				</view>

				<image class="arrow" src='../../static/icon15.png' />
			</view>
			<view class="list-item" @click="handleTorealName">
				<view class="left">
					<image class="icon" src='../../static/my-name.png' />
					<view class="label">实名认证</view>
				</view>

				<image class="arrow" src='../../static/icon15.png' />
			</view>
			<view class="list-item" @click="handleSecondQi">
				<view class="left">
					<image class="icon" src='../../static/my-send.png' />
					<view class="label">专属快递员</view>
				</view>

				<image class="arrow" src='../../static/icon15.png' />
			</view>
		</view>

		<view class="list-bottom" v-if="token">
			<picker mode="selector" @change="changeSex" :value="sex" :range="sexList">
				<view class="list-item">
					<view class="label">性别</view>
					<image class="arrow" src='../../static/icon15.png' />
				</view>
			</picker>
			<picker mode="date" :value="birthday" :start="startDate" :end="endDate" @change="getDateChange">
				<view class="list-item">
					<view class="label">生日</view>
					<image class="arrow" src='../../static/icon15.png' />
				</view>
			</picker>
		</view>
	</view>
	<!-- footer -->
	<UniFooter :pagePath="'pages/my/index'"></UniFooter>
	<!-- end -->
</template>

<script setup>
	import {
		ref,
		reactive,
		onMounted
	} from 'vue';
	import {
		getUserInfo,
		getRealNameStatusApi,
		updateUserInfo
	} from '@/pages/api/my.js';
	import {
		handleSecondQi
	} from '@/utils/index.js'
	// import { useStore } from 'vuex';
	const token = ref()
	const sexList = reactive(['女', '男'])
	let nickName = ref('')
	let avatarUrl = ref('')
	let startDate = ref()
	let endDate = ref()
	let idCard = ref('')
	let name = ref('')
	let sex = ref(1)
	let birthday = ref('1995-09-01')
	const alertDialog = ref()
	const isRealNameAuth = ref(false)
	// 底部导航
	import UniFooter from '@/components/uni-footer/index.vue';
	onMounted(() => {
		startDate.value = getDate('start')
		endDate.value = getDate('end')
		baseUserInfo()
		token.value = uni.getStorageSync('token')
		nickName.value = uni.getStorageSync('nickName') || '神领用户'
		avatarUrl.value = uni.getStorageSync('avatarUrl') || '../../static/defaultHeadImg.png'
		// test()
	})

	const baseUserInfo = () => {
		getUserInfo().then((res) => {
			console.log(res, 'res')
			isRealNameAuth.value = Boolean(res.data.idCardNoVerify)
			idCard.value = res.data.idCard || ''
			name.value = res.data.name
			sex.value = res.data.sex 
			birthday.value = res.data.birthday                 
		})
	}
	const getUserInfoFunc = () => {
		uni.getUserProfile({
			desc: 'Wexin',
			success: (res) => {
				nickName.value = res.userInfo.nickName
				avatarUrl.value = res.userInfo.avatarUrl
				uni.setStorageSync('nickName', res.userInfo.nickName);
				uni.setStorageSync('avatarUrl', res.userInfo.avatarUrl);
				console.log(res, '个人信息')
			},
			fail: () => {
				uni.showToast({
					title: "个人信息获取失败",
					icon: 'none',
					duration: 1000
				})
			}
		})
	}
	const getDate = (type) => {
		const date = new Date()
		let year = date.getFullYear()
		let month = date.getMonth() + 1
		let day = date.getDate()
		if (type === "start") {
			year = year - 60
		} else {
			year = year + 2
		}
		month = month > 9 ? month : "0" + month
		day = day > 9 ? day : '0' + day
		return `${year}-${month}-${day}`

	}
	const toLogin = () => {
		if (uni.getStorageSync('token')) {
			getUserInfoFunc()
		} else {
			uni.navigateTo({
				url: '/pages/login/index'
			});
		}


	}
	//跳转到地址簿
	const handleToAddress = () => {
		uni.navigateTo({
			url: '/pages/address/index?type=address'
		});
	}
	//跳转到实名认证页面
	const handleTorealName = () => {
		if (isRealNameAuth.value) {
			uni.navigateTo({
				url: '/subPages/authentication-success/index?name=' + name.value + '&idCard=' + idCard.value
			});
		} else {
			uni.navigateTo({
				url: '/subPages/realName-authentication/index'
			});
		}
	}

	const getDateChange = (e) => {
		console.log(e, 'getDateChange')
		updateUserInfo({
			birthday: e.detail.value
		}).then((res) => {
			baseUserInfo()
			console.log(res, 'res')
		})
	}

	const changeSex = (e) => {
		console.log(e, 'changeSex')
		updateUserInfo({
			sex: Number(e.detail.value)
		}).then((res) => {
			baseUserInfo()
			console.log(res, 'res')
		})
	}
</script>
<style src="./index.scss" lang="scss" scoped></style>

